@charset 'utf-8';
@import '../base/global.scss';
/* 
 * content.scss
 * 内容提供
*/

// content图片路径
$current-img-path: '../' + $images-path;


.item .input-text.w-136,
.item .input-select.w-136 {
  width: 136px;
}

.item .input-text.w-100,
.item .input-select.w-100 {
  width: 100px;
}





.tabSwitch2 {
  cursor: pointer;
}

/* 查看合计 */
.viewCount {
  min-width: 460px;
  font-family: "PingFang SC", "Microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  padding: 30px 50px;
  position: relative;
  box-sizing: border-box;
}

.viewCount .label,
.viewCount .value {
  float: left;
  word-wrap: break-word;
  text-align: left;
}

.viewCount .label {
  text-align: center;
  margin: 6px 0;
  width: 90px;
  color: #333;
}
.viewCount .value {
  color: #666;
  margin: 6px 0;
}

/* 提示 */
.tips h1 {
  font-size: 1.4em;
  margin-bottom: 0.5em;
  color: $Theme-text-primary;
}

/* 入网产品 */
.subTitle,
.productItem,
.ruleItem,
.routerItem {
  transition: none;
  padding: 0 0.5em;
  .title-h2 {
    border-left: 3px solid #009688;
  }
}

/* 路由模板 */
.ruleItem {
  padding-top: 1.5em;
  padding-bottom: 1em;
}
.ruleItem:first-child {
  padding-top: 0;
}
.ruleItem:last-child {
  border-bottom: none;
}

/* 图表切换按钮 */
.toggle-box {
  position: absolute;
  z-index: 10;
  left: 10px;
  bottom: 10px;
}
.toggle-span.amount {
  cursor: pointer;
  float: left;
  display: inline-block;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: #555;
  text-align: center;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 4px 0 0 4px;
  border-right: none;
}
.toggle-span.count {
  cursor: pointer;
  float: left;
  display: inline-block;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: #555;
  text-align: center;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 0 4px 4px 0;
}
.toggle-span:hover {
  color: black;
  background-color: #ddd;
}
.toggle-span.active {
  color: black;
  background-color: #ddd;
}


/* 复选框组 */
  .checkbox-list {
    margin-left: 5em;
  }
  .checkbox-wrap {
    float: left;
    padding: 0.5em;
  }
  .checkbox-group {
    padding-top: 1em;
    padding-bottom: 1em;
    /*border-bottom: 1px dashed #D9D9D9;*/
  }
  .checkbox-group:first-child {
    padding-top: 0;
  }
  .checkbox-group:last-child {
    padding-bottom: 0;
    border-bottom: none;
  }

  @media only screen and (max-width: 250px) {
    .checkbox-wrap {
      width: 100%;
    }
  }
  @media only screen and (min-width: 251px) {
    .checkbox-wrap {
      width: 50%;
    }
  }
  @media only screen and (min-width: 500px) {
    .checkbox-wrap {
      width: 33.33%;
    }
  }
  @media only screen and (min-width: 1000px) {
    .checkbox-wrap {
      width: 25%;
    }
  }





/* 网点管理器 */
.shopMannage {
  .title {
    font-size: 1.7em;
  }
  .addShop {
    color: #03a9f4;
    font-size: 1.5em;
    vertical-align: middle;
    cursor: pointer;
  }
  .shopItem .item  {
    float: left;
    margin-right: 0.5em;
  }
}


/* 首页 */
.h-315 {
  height: 315px;
}
.h-320 {
  height: 320px;
}

.area-block {
  height: 100%;
  background: white;

  .title {
    @extend %fix;
    color: $Theme-text-primary;
    padding: 5px 20px;
    border-bottom: 1px solid #eae7e7;
    line-height: 2em;

    .primary {
      color: $Theme-text-primary;
      font-size: 1.4em;
    }

  }

  .plane {
    -webkit-overflow-scrolling: touch;
    color: $Theme-text-secondary;
  }

  .line {
    @extend %ib;
    width: 1px;
    height: 1em;
    margin: 0 10px;
    background: #9E9E9E;
  }


}
.filter-item {
  font-size: 0.9em;
  display: inline-block;
  color: $Theme-text-primary;
  vertical-align: middle;
  &.active {
    color: $Theme-color;
  }
}


.color-green {
  color: #1abc9c;
}
.color-blue {
  color: #297cbb;
}
.color-red {
  color: #F44336;
}
.color-orange {
  color: #f39c12;
}
.bg-green {
  background-color: #1abc9c;
}
.bg-orange {
  background-color: #f39c12;
}
.bg-blue {
  background-color: #297cbb;
}
.bg-red {
  background-color: #E91E63;
}
.bg-yellow  {
  background-color: #f1c40f;
}

.tag {
  padding: 0 0.6em;
  border-radius: 0.6em;
  color: #fff;
}



.welcome {
  background: #f5f5f5;
  max-width: 100vw;

  // 个人信息
  .decs {
    padding: 1.5em 0.4em 0.4em;

    // 头像
    .avatar {
      width: 65px;
      float: left;
      // display: inline-block;
      vertical-align: text-bottom;
    }
    .avatar-img {
      width: 65px;
      height: 65px;
      background: url($current-img-path + 'backindex1.png') no-repeat;
      background-position: -3px 0;
      background-size: 70px 110px;
    }

    // 详细信息
    .detailed {
      font-size: 12px;
      overflow: hidden;
      // display: inline-block;
      vertical-align: top;
      margin-top: 0.2em;
      line-height: 1.7;
    }

    .company-name {
      color: $Theme-text-primary;
    }

    .line {
      @extend %ib;
      width: 1px;
      height: 1em;
      margin: 0 10px;
      background: #9E9E9E;
    }

    .authenticate {
      line-height: 1.2;
      .ico {
        @extend %ib;
        font-size: 1.5em;
        color: $Theme-color;
      }
    }

    .amount-count {
      line-height: 1.9em;
      color: $Theme-text-third;
      .depict {
        @extend %ib;
        width: 100px;
        color: $Theme-text-secondary;
      }
      .amount {
        @extend %ib;
        margin-left: 1em;
      }

    }



  }

  // 今日数据统计
  .statistics {
    font-size: 1.1em;
    padding: 0.6em;

    li {
      margin-bottom: 0.4em;
    }

    .ico {
      display: inline-block;
      vertical-align: text-bottom;
      width: 0.6em;
      height: 0.6em;
      border-radius: 50%;
      margin: 0 0.4em;
    }

    .content {
      display: inline-block;
      vertical-align: text-top;
      line-height: 2;
    }

    .depict {
      color: $Theme-text-primary;
    }
    .count span{
      margin-right: 0.2em;
    }


  }

  // 公告列表
  .bulletin {
    iframe {
      width: 100%;
      height: 275px;
    }
  }

  // 图表
  .chart {
    padding: 1em 1em 0;
    height: 280px;
  }

  @media screen and (max-width: 1120px) {
    .col-1.w-p-70,
    .col-2.w-p-30 {
      width: 100%;
    }

    .information.w-p-65 {
      width: 100%;
      height: 330px;
      margin-bottom: 0.8em;
    }
    .today-data.w-p-35 {
      width: 100%;
      margin-bottom: 0.8em;
    }

    #pay-and-bay-chart,
    #analysis-chart {
      margin-bottom: 0.8em;
    }

    .bulletin {
      iframe {
        // height: 375px;
      }
    }


    .h-315 {
      height: 320px;
    }
    .h-320 {
      height: 320px;
    }

    .area-block.mr-10,
    .h-320.mr-10 {
      margin-right: 0;
    }

    .decs {
      .amount-count {
        width: 900px;
        .depict {
          width: 130px;
        }
      }
    }




  }






}

/* 新闻列表 */
.bulletinList {
  .circle {
    background: #e0dddd;
    display: inline-block;
    position: absolute;
    left: 0px;
    top: 15px;
    width: 7px;
    height: 7px;
    border-radius: 7px;
  }

  .list {
    margin: 10px 20px 5px 20px;
    padding: 0;
    line-height: initial;
  }

  li {
    position: relative;
    padding: 10px 0 10px 15px;
    width: 100%;
    a {
      cursor: pointer;
      display: block;
      overflow: hidden;
      line-height: 18px;
      @extend %ellipsis;
    }

    .text {
      @extend %ib;
      @extend %ellipsis;
      width: 75%;
    }
    .plus {
      @extend %ib;
      width: 22%;
    }

  }

}

/* 权限管理 */
.role-group {
  padding: 0.8em;
  padding-top: 0.8em;
  font-size: 1.1em;
}
.role-group .row {
  padding-bottom: 2em;
  padding-top: 1.2em;
  border-bottom: 1px dashed #ddd;
}
.role-group .row:first-child {
  padding-top: 0;
}
.role-group .row:last-child {
  border-bottom: none;
}
.primary > .check-list,
.secondary-row > .check-list {
  float: left;
  width: 156px;
}
.sub {
  position: relative;
  overflow: hidden;
}
.featur {
  float: left;
  width: 162px;
  overflow: hidden;
  margin: 0 0.8em;
}


/* 商户详情 */
.detail {
  @extend %fix;
  color: $Theme-text-third;
  line-height: 2.5;

  .col {
    float: left;
  }

  .row {
    @extend %fix;
  }

  .label {
    width: 100px;
    float: left;
    text-align: right;
    margin-right: 20px;
    color: $Theme-text-primary;
    word-break: break-word;
  }
  .value {
    float: left;
    width: calc(100% - 120px);
    word-wrap: break-word;
    word-break: break-word;
  }


}

/* 证件信息 */
.img-box {
  padding: 0.8em;
  img {
    max-width: 100%;
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
    cursor: pointer;
  }
}
.desc {
  text-align: center;
  color: $Theme-text-secondary;
  margin-bottom: 0.5em;
}
.look-loading-img {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  filter: gray;
}

/* 修改证件信息图片 */
.id-phone {

  li {
    line-height: 35px;
  }


  .ico {
    vertical-align: sub;
    // visibility: hidden;
    font-size: 1.5em;
    margin: 0 0.2em;
    color: #297cbb;
    .fa:before {
      content: "\f15b";
    }
  }

  li.modify .ico {
    color: rgb(255, 153, 0);
    visibility: visible;
    .fa:before {
      content: "\f06a";
    }
  }



}



.productType {
  margin-bottom: 0.8em;
}

/* 列表 */
.list {
  padding: 0.8em;
  line-height: 2.5;

  li {
    @extend %fix;
  }

  .info-title {
    float: left;
    width: 100px;
    text-align: right;
    color: $Theme-text-primary;
    margin-right: 20px;
  }
  .info-value {
    float: left;
    @extend %ellipsis;
    width: calc(100% - 120px);
    color: $Theme-text-third;
  }
  // 列表值换行
  .info-value.break-word {
    word-wrap: break-word;
    white-space: initial;
  }
}

.crt-list li {
  width: 270px;
  margin: auto;
  padding: 0.5em 1em;
  text-align: center;
}
.crt-list .pre-text {
  width: 108px;
  display: inline-block;
}

/* 模拟表格 */
  .table-info {
    display: table;
    border: 1px solid #e4e4e4;
  }
  .table-info .type-td {
    padding: 10px;
  }
  .table-info .type-tr:nth-child(2n) {
    background-color: #ecf0f1;
  }
  .type-table {
    display: table;
  }
  .type-tr {
    display: table-row;
  }
  .type-td {
    display: table-cell;
    word-break: break-all;
    vertical-align: top;
  }

  /* 文档详情 */
  .document-title {
    padding: 0.8em;
    font-size: 1.2em;
    text-align: center;
    border-bottom: 1px solid #ddd;
  }
  .document-title .title {
    font-size: 30px;
    font-weight: bold;
  }
  .date-effective {
    text-align: center;
  }
  .date-effective {
    color: #F44336;
    font-weight: bold;
    font-size: 14px;
    padding: 6px 0;
  }
  .date-effective .date-label {
    color: #9a9090;
    font-size: 12px;
  }
  .document-content {
    min-height: 300px;
  }
  .document-content {
    padding: 10px 10px 5px;
  }
  .floatFixed {
    position: fixed;
    background-color: #fff;
    width: 100%;
    top: 0;
    z-index: 1;
  }
  .topMargin {
    margin-top: 100px;
  }



  /* 菜单树 */
  .menu-tree {
    border: 1px solid #ccc;
    min-height: 500px;
    height: 800px;
    overflow: auto;
  }

  .tree-ul {
    padding: 10px;
    line-height: 1.7;
  }
  .tree-ul .folder-ico:before {
    content: "\f07b";
  }
  .tree-ul a {
    font-size: 14px;
    padding: 0 6px;
    color: #333;
  }
  .tree-ul li {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    line-height: 30px;
  }
  .tree-ul .fa {
    font-size: 14px;
    color: #444;
  }
  .tree-ul .fa + span {
    margin-left: 8px;
  }

  .sub-menus {
    display: none;
    margin-left: 20px;
  }
  .sub-menus .fa + span {
    color: #333;
  }


  .open > a > .folder-ico:before {
    content: "\f07c";
  }
  .open .sub-menus {
    display: block;
  }

  .tree-ul .file-ico:before {
    content: "\f0f6";
  }

  .tree-ul a:hover > span {
    color: #71b2d0;
  }

  .tree-ul li.active > a > span {
    color: #03A9F4;
  }


.w-375 {
  width: 375px;
}

.toptips {
  display: none;
  position: fixed;
  transform: translateZ(0);
  top: 0;
  left: 0;
  right: 0;
  padding: 10px;
  font-size: 14px;
  text-align: center;
  color: #fff;
  z-index: 5000;
  word-wrap: break-word;
  word-break: break-all;
}
.toptips_warn {
  background-color: #da5f5d;
}